@font-face {
  font-family: 'chevron_up';
  src: url(data:application/font-woff2;charset=utf-8;base64,) format('woff2'), url(data:application/font-woff;charset=utf-8;base64,) format('woff');
  font-weight: normal;
  font-style: normal;
}

@mixin chevron_up {
  font-family: 'chevron_up';
  &:before {
    content: '\f077';
    font-style: normal;
  }
}

.chevron_up {
  @include chevron_up;
  display: inline-block;
}
