@font-face {
  font-family: 'times_circle';
  src: url(data:font/truetype;charset=utf-8;base64,) format('truetype');
  font-weight: normal;
  font-style: normal;
}


@mixin times-circle {
  font-family: 'times_circle';
  &:before {
    content: '\f057';
    font-style: normal;
  }
}

.times-circle {
  @include times-circle;
  display: inline-block;
}
