/* Edited for Bridge Theme */

$sc-color-icon: $sc-neutral-shade-0;
$sc-color-star-filled: #f9b710;
$sc-color-star-empty: $sc-color-theme-border-light;
$sc-color-star-error: $sc-color-error;
// Base icons
//
// Icons are a visual representation of an object, action, or idea that communicate meaning in a graphical way.
//
// Markup:
// <i class="icon-home"></i>
// <i class="icon-star"></i>
// <i class="icon-like"></i>
//
// Styleguide 2.4.1
.icon-container {
    @include border-radius(50%);
    background-color: $sc-color-theme-500;
    display: inline-block;
    text-align: center;
    height: 20px;
    width: 20px;
    line-height: 18px;
}

.icon {
    @extend .fa;
    color: $sc-color-icon;
    font-size: 13px;
    vertical-align: middle;
    display: inline;
    font-style: normal;
}

.icon-base {
    @extend .fa;
    font-size: 20px;
    display: inline;
}

.icon-display-list {
    @extend .fa;
    @extend %fa-navicon;
    border-radius: 5px;
    border-width: 1px;
    border-color: black;
    height: 45px;
}

.icon-display-table {
    @extend .fa;
    @extend %fa-th-large;
}

.icon-display-grid {
    @extend .fa;
    @extend %fa-th;
}

.icon-close {
    @extend .icon;
    @extend %fa-close;
}

.icon-remove {
    @extend .icon;
    @extend %fa-close;
}

.icon-container-info {
    @extend .icon-container;
    background-color: $sc-color-info;
}

.icon-info {
    @extend .icon;
    @extend %fa-info-circle;
}

.icon-question {
    @extend .icon;
    @extend %fa-question;
}

.icon-question-circle {
    @extend .icon;
