.cart-detailed{
	@extend .cart;

	// .transaction-line-views-selected-option-color-label-text,
	// .transaction-line-views-tax-label,
	// .cart-item-summary-item-list-actionable-amount-label,
	// .cart-item-summary-item-list-actionable-label-qty{
	// 	color: $sc-color-copy-dark;
	// 	font-weight: 600;
	// }

	// .transaction-line-views-selected-option {
	// 	.transaction-line-views-selected-option-label{
	// 		color: $sc-color-copy-dark;
	// 		font-weight: 600;
	// 	}
    // }
    
    & .transaction-line-views-selected-option-color-label-text,
	& .cart-item-summary-item-list-actionable-label-qty{
		color: $sc-color-copy;
		font-weight: $sc-font-weight-bold;
	}

	& .cart-item-summary-item-list-actionable-amount-label,
	& .cart-item-summary-amount-value {
		color: $sc-color-copy;
		font-weight: $sc-font-weight-bold;
		@media (min-width: $screen-md-min) {
			color: $sc-color-theme-dark;
			font-size: $sc-font-size-xs;
		}
	}

	& .transaction-line-views-selected-option {
		.transaction-line-views-selected-option-label{
			color: $sc-color-copy;
			font-weight: $sc-font-weight-bold;
		}
	}
	& .cart-summary-grid .cart-summary-expander-head-toggle {
		border-bottom: 0;
	}
}

