//Edited for Bridge

.product-list-control-button-move{
	@extend .button-tertiary;
	@extend .button-small;
	min-width: 100px;
	text-align: center;
	margin-top: 10px;
}

.product-list-control-button-wishlist{
	@extend .button-tertiary;
	@extend .button-large;
	margin: $sc-margin-lv3 auto 0;
	font-weight: $sc-font-weight-semibold;
	width: 80%;
	text-align:center;
	cursor:pointer;
	cursor:hand;
	display: block;
	font-size: 14px;
	padding: 0 15px;
	border: 2px solid $sc-color-link;
	line-height: $sc-target-size-mobile - 4px;
	@media(min-width: $screen-sm-min){
		display: inline-block;
		margin: 0;
		margin-top: $sc-margin-lv3;
		width: 100%;
		padding: 17px 25px;
		font-size: 1.067rem;
	}
}

.product-list-control-button-wishlist:focus {
	outline:0;
	-webkit-box-shadow:inset 0 1px 1px transparentize($sc-neutral-shade-500, 1-0.075),0 0 8px transparentize($sc-color-theme-border, 1-0.6);
	box-shadow:inset 0 1px 1px transparentize($sc-neutral-shade-500, 1-0.075),0 0 8px transparentize($sc-color-theme-border, 1-0.6);
}

.product-list-control-flyout{
	text-align: left;
	border:1px solid $sc-color-secondary;
	box-shadow: 0 6px 12px transparentize($sc-neutral-shade-500, 1-0.175);
	position:relative;
	width:100%;
	background:$sc-neutral-shade-0;
	z-index:99999999999;

	.global-views-message {
		margin-bottom: 0;
		border-radius: 0;
	}

	@media(min-width: $screen-sm-min){
		position: absolute;
		z-index: 1;
		margin-top: $sc-margin-lv2;
	}
}

.product-list-control-flyout-title{
	padding: $sc-padding-lv3;
	border-bottom:1px solid $sc-color-secondary;
}

.product-list-control-flyout-product-lists{
	padding: $sc-padding-lv3;
	padding-bottom: 0;

	li{
		font-weight:$sc-font-weight-normal;
		color:$sc-color-theme-700;
		font-size:$sc-font-size-m;
	}
}

.product-list-control-new-product-list-container{
}

.product-list-control-nolists-messages{}
.product-list-control-move{
	min-width: 250px;
	right: 0;
}
