$sc-margin-lv3: $sc-margin-base * 3;
$sc-margin-lv4: $sc-margin-base * 4;
$sc-margin-lv5: $sc-margin-base * 5;
$sc-margin-lv6: $sc-margin-base * 6;
$sc-margin-lv7: $sc-margin-base * 7;
$sc-margin-lv8: $sc-margin-base * 8;

// LEGACY VARIABLES - used for the Bridge Theme
// SIZING
$sc-theme-cell-img-height: 210px; // image size on merchandising zones, related items etc
$sc-theme-cell-img-width: 210px;
$sc-theme-skew-distance: 0px; // MUST INCLUDE PX, EVEN IF 0! distance of slant on buttons and nav
$sc-theme-border-radius: 0px; // curve radius on all container divs and buttons

// SPACING
// For left/right padding
$sc-theme-mobile-padding: 25px;
$sc-theme-desktop-padding: 60px;
