@font-face {
  font-family: 'double_angle_up';
  src: url(data:font/truetype;charset=utf-8;base64,) format('truetype');
  font-weight: normal;
  font-style: normal;
}


@mixin double-angle-up {
  font-family: 'double_angle_up';
  &:before {
    content: '\f102';
    font-style: normal;
  }
}

.double-angle-up {
  @include double-angle-up;
  display: inline-block;
}
